.modal {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  z-index: 10;
}

.modal-background {
  background: rgba(0, 0, 0, .5);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.modal-content {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: white;
  padding: 1em;
  min-width: 10vw;
  min-height: 10vh;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.modal-opened {
  opacity: 1;
}

.modal-closed {
  opacity: 0;
  pointer-events: none;
}



